  margin: 40px auto; }

.accordion {
  background-color: #eee;
  color: #fff;
  background: #231F20;
  border-bottom: 1px solid #fff;
  position: relative;
  cursor: pointer;
  padding: 28px 48px 28px 18px;
  width: 100%;
  text-align: left;
  border: none;
